
import React, {useRef, useEffect } from 'react';
import * as THREE from 'three';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';
import { FBXLoader } from 'three/examples/jsm/loaders/FBXLoader.js';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';
import { LineMaterial } from 'three/examples/jsm/lines/LineMaterial.js';
import {Wireframe} from 'three/examples/jsm/lines/Wireframe.js';
import {WireframeGeometry2} from 'three/examples/jsm/lines/WireframeGeometry2.js';
import styled from 'styled-components/macro';
import { FontLoader } from 'three/examples/jsm/loaders/FontLoader.js';
import { TextGeometry } from 'three/examples/jsm/geometries/TextGeometry.js';

export const IntroWorld = () => {
  const ref = useRef();
  const renderer = new THREE.WebGLRenderer({antialias:true, alpha:true});
  const scene = new THREE.Scene();

  useEffect(() => {
    const URL_PREFIX = ""; //"http://localhost:8080";
    const loader = new FBXLoader();
    const model = `${URL_PREFIX}/models/boy.fbx`;

    // scene.background = new THREE.Color( 0x1f004c );
    // scene.fog = new THREE.FogExp2( 0x1f004c, 0.002 );

    let mixer;

    const width = 500;
    const height = 500;

    const size = 10;
    const divisions = 1;
    let font;

    const gridHelper = new THREE.GridHelper( size, divisions );
    gridHelper.material.color.setHex(0xD5AD6D)
    gridHelper.position.y=-8;
    scene.add(gridHelper)

    const camera = new THREE.PerspectiveCamera( 30, width / height, 1, 500 );
    camera.position.set( 10, 0, 50 );


    const fontLoader = new FontLoader();
    fontLoader.load( `${URL_PREFIX}/fonts/helvetiker_bold.typeface.json`, function ( response ) {

					font = response;

          const materials = [
            new THREE.MeshPhongMaterial( { color: 0xD5AD6D, flatShading: true } ), // front
            new THREE.MeshPhongMaterial( { color: 0xD5AD6D } ) // side
          ];

          const textGeo = new TextGeometry( "Play with me!", {
            font: font,
            size: 100,
            height: 5,
            curveSegments: 10,
            bevelEnabled: false
          } );
      
          textGeo.computeBoundingBox();
      
          const textMesh1 = new THREE.Mesh( textGeo, materials );
          textMesh1.scale.x = 0.01;
          textMesh1.scale.y = 0.01;
          textMesh1.scale.z = 0.01;
          textMesh1.position.z = 5;
          textMesh1.position.x = -4.5;
          textMesh1.position.y = -9.5;
          scene.add( textMesh1 );

          const textGeo2 = new TextGeometry( "Together in", {
            font: font,
            size: 100,
            height: 5,
            curveSegments: 10,
            bevelEnabled: false
          } );
      
          textGeo2.computeBoundingBox();
      
          const textMesh2 = new THREE.Mesh( textGeo2, materials );
          textMesh2.scale.x = 0.01;
          textMesh2.scale.y = 0.01;
          textMesh2.scale.z = 0.01;
          textMesh2.position.z = 4;
          textMesh2.position.x = 5;
          textMesh2.position.y = -9.5;

          textMesh2.rotation.y =  Math.PI / 2;
          scene.add( textMesh2 );

          const textGeo3 = new TextGeometry( "KhoLand", {
            font: font,
            size: 100,
            height: 5,
            curveSegments: 10,
            bevelEnabled: false
          } );
      
          textGeo3.computeBoundingBox();
      
          const textMesh3 = new THREE.Mesh( textGeo3, materials );
          textMesh3.scale.x = 0.01;
          textMesh3.scale.y = 0.01;
          textMesh3.scale.z = 0.01;
          textMesh3.position.z = -5;
          textMesh3.position.x = 3;
          textMesh3.position.y = -9.5;

          textMesh3.rotation.y =  -Math.PI ;
          scene.add( textMesh3 );

				} );


   

    loader.load( model, function ( fbx ) {
      scene.add( fbx );
      fbx.position.y=-8.5;
      fbx.scale.x = 10;
      fbx.scale.y = 10;
      fbx.scale.z = 10;
      const lookAt = fbx.position.clone()
      lookAt.x += 50;
      camera.lookAt(lookAt);


      mixer = new THREE.AnimationMixer(fbx);

      loader.load(`${URL_PREFIX}/models/hip-hop-dance.fbx`,(object1) => {
          console.log('loaded samba', object1);
          const animationAction = mixer.clipAction(object1.animations[0])
          animationAction.play();
      });

  }, undefined, function ( error ) {
      console.error( error );
  } );
    
    renderer.setSize(width, height);

    ref.current.appendChild(renderer.domElement);

    // controls
    const controls = new OrbitControls( camera, renderer.domElement );
    controls.listenToKeyEvents( window ); // optional

    controls.enableDamping = true; // an animation loop is required when either damping or auto-rotation are enabled
    controls.dampingFactor = 0.05;
    controls.screenSpacePanning = false;
    controls.minDistance = 20;
    controls.maxDistance = 45;
    controls.minPolarAngle = Math.PI /4;
    controls.maxPolarAngle = Math.PI /2;
    controls.autoRotate = true;
    controls.enableZoom = false;
    controls.autoRotateSpeed = -1;

    const dirLight1 = new THREE.DirectionalLight( 0xffffff );
    const dirLight2 = new THREE.DirectionalLight( 0xffffff  );
    const dirLight3 = new THREE.DirectionalLight( 0xffffff  );
    const dirLight4 = new THREE.DirectionalLight( 0xffffff  );
    const dirLight5 = new THREE.DirectionalLight( 0xffffff );
    const ambientLight = new THREE.AmbientLight( 0xfcfcfc );

    dirLight1.position.set( 0,  /*top to bottom*/ 1, 0);
    dirLight2.position.set( 1, 1, 0 );
    dirLight3.position.set( 0, 1, 1 );
    dirLight4.position.set(  -1, 1, 0 );
    dirLight5.position.set(  0, 1, -1 );
    
    scene.add( dirLight1 );
    // scene.add( dirLight2 );
    // scene.add( dirLight3 );
    // scene.add( dirLight4 );
    // scene.add( dirLight5 );
    scene.add( ambientLight );

    let clock = new THREE.Clock();

    const render = () =>{
      renderer.render(scene, camera);
    }

    const animate = () => {
      requestAnimationFrame( animate );

      if(mixer){
        mixer.update(clock.getDelta())
    }
      controls.update(); // only required if controls.enableDamping = true, or if controls.autoRotate = true
      render();
    }

    animate();

    // let autorotateTimeout;
    // // stop autorotate after the first interaction
    // controls.addEventListener('start', function(){
    //   clearTimeout(autorotateTimeout);
    //   controls.autoRotate = false;
    // });

    // // restart autorotate after the last interaction & an idle time has passed
    // controls.addEventListener('end', function(){
    //   autorotateTimeout = setTimeout(function(){
    //     controls.autoRotate = true;
    //   }, 1000);
    // });


    return () => {
        // Callback to cleanup three js, cancel animationFrame, etc
    }
  }, []);

  return <GrabDiv ref={ref} />;
}

const GrabDiv = styled.div`
	color: #000;
	:hover {
		color: #ed1212;
		cursor: grab;
	}
` 